import React from 'react'
import banner from '../images/bannera.jpg'
import avatar from '../images/avatar.jpg'

const Hero = () => {
    return (
        <>
        <div name="section1b"></div>
            <section className="text-cinder-200 bg-cinder-900 body-font sm:mt-8">
                <div className="container px-5 pb-24 mx-auto flex flex-col">
                    <div className="lg:w-4/4 mx-auto">
                        <div className="rounded-lg h-64 overflow-hidden">
                            <img alt="#" className="object-cover object-center h-full w-full" src={banner}/>
                        </div>
                        <div className="flex flex-col sm:flex-row mt-10">
                            <div className="sm:w-1/3 text-center sm:pr-8 sm:py-8">
                                <div className="w-20 h-20 rounded-full inline-flex items-center justify-center bg-gray-800 text-gray-600">
                                    <img alt='avatar' src={avatar} className='w-full rounded-full' />
                                </div>
                                <div className="flex flex-col items-center text-center justify-center">
                                    <h2 className="font-medium title-font mt-4 text-white text-lg">Ailén Glassmam</h2>
                                    <div className="w-12 h-1 bg-yellow-500 rounded mt-2 mb-4"></div>
                                    <p className="text-base text-gray-400">Diseñadora Industrial graduada de la Universidad Nacional de La Plata y actualmente formándome como Desarrolladora Web Full Stack 🤓</p>
                                </div>
                            </div>
                            <div className="sm:w-2/3 sm:pl-8 sm:py-8 sm:border-l border-gray-800 sm:border-t-0 border-t mt-4 pt-4 sm:mt-0 text-center sm:text-left">
                                <p className="leading-relaxed text-lg mb-4">
                                    Seguramente te preguntarás ¿Por qué una diseñadora industrial está buscando un empleo en desarrollo web? 👀
                                    <br className='my-2' />
                                    <br className='my-2' />
                                    Durante mi etapa universitaria tuve la oportunidad de desarrollar diferentes habilidades, tales como investigación, gestión de proyecto, proyección de ideas, solución de problemas y trabajo en equipo. Gracias a esto, tuve la oportunidad de identificar en qué áreas, dentro de las diferentes etapas de un proyecto, me sentía más confiada, con mayor entusiasmo y ganas de aprender. Casualmente identifiqué que me apasionaba la parte proyectual: diagramar, pensar en qué herramientas tengo disponibles y cuales debo aprender, la capacidad de llevar un dibujo a mano alzada a un elemento 3D / 2D, pensar en alternativas, modificar, jugar con los colores, y mil cosas más dentro de lo que es el mundo visual y digital. Además, en diferentes oportunidades, la institución nos permitió tener un acercamiento hacia en mundo IT ya sea desde la experiencia de usuario como a la posibilidad de brindar soluciones a la sociedad con productos "no tangibles".
                                    <br className='my-2' />
                                    <br className='my-2' />
                                    Es por ello que, desde finales del 2021 y durante todo el 2022 comencé a realizar diferentes capacitaciones para migrar hacia el mundo del desarrollo web. Fue y continua siendo un proceso de aprendizaje constante. Comencé con videos de YouTube para ver si era capaz de lograr este cambio, luego me dediqué a investigar capacitaciones gratuitas y pagas para poder tener una base teórica firme.
                                    <br className='my-2' />
                                    <br className='my-2' />
                                    Hoy por hoy, mi objetivo principal es tener la oportunidad de comenzar a desarrollar mi carrera profesional desde las bases, como Desarrolladora Front End (porque si, me apasiona y encuentro todo un desafío el poder llevar un mockup o diseño plano a algo funcional por medio de líneas de código), y luego ir creciendo progresivamente.
                                    </p>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}

export default Hero