import React from 'react'
import { projects } from '../resources/dataProjects'

const Projects = () => {
  return (
    <>
      <div id='projects' name="section2"></div>
      <section className="text-cinder-200 bg-projects-background bg-center bg-cover body-font">
        <div className="container px-5 py-24 mx-auto">
          <div className="flex flex-col">
            <div className="h-1 bg-gray-800 rounded overflow-hidden">
              <div className="w-24 h-full bg-yellow-500"></div>
            </div>
            <div className="flex flex-wrap sm:flex-row flex-col py-6 mb-12">
              <h2 className="sm:w-2/5 text-white font-medium title-font text-2xl mb-2 sm:mb-0">Proyectos realizados</h2>
              <p className="sm:w-3/5 leading-relaxed text-base sm:pl-10 pl-0">Durante mi paso por las distintas capacitaciones en las cuales participé, tuve la oportunidad de desarrollar diferentes proyectos desde el desarrollo exclusivo del Front End como así también, del desarrollo Back End. Proyectos de código puro y otros empleando frameworks.</p>
            </div>
          </div>
          <div className="flex flex-wrap -m-4">
            {/* adding logic to map the array */}
            {projects.map((project) => (
              <div className="lg:w-1/3 sm:w-1/2 p-4 ">
                <div className="flex relative">
                  <div className="px-8 py-10 relative w-full md:h-80 sm:h-auto border-2 border-white rounded-lg bg-cinder-900">
                    <h3 className="tracking-widest text-sm title-font font-medium text-scooter-400 mb-1">{project.title}</h3>
                    <h2 className="title-font text-lg font-medium text-white mb-3">{project.subtitle}</h2>
                    <p className="leading-relaxed md:h-24 pr-3 md:overflow-y-scroll scrollbar-thin scrollbar-thumb-yellow-400 scrollbar-track-gray-900">{project.description}</p>
                      <a className="text-indigo-500 inline-flex items-center md:mt-3 lg:mb-0" href={project.link} target="_blank" key={project.id} rel="noreferrer">Ver proyecto
                        <svg className="w-4 h-4 ml-2" viewBox="0 0 24 24" stroke="currentColor" stroke-width="2" fill="none" stroke-linecap="round" stroke-linejoin="round">
                          <path d="M5 12h14"></path>
                          <path d="M12 5l7 7-7 7"></path>
                        </svg>
                      </a>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    </>
  )
}

export default Projects