import React from 'react'
import { Link } from 'react-scroll'

const Navbar = () => {

    return (
        <>
            <header className="text-gray-400 bg-cinder-900 body-font fixed top-0 left-0 right-0 z-10">
                <div className="container mx-auto flex flex-wrap p-5 flex-col md:flex-row items-center">
                    <nav className="md:ml-auto flex flex-wrap items-center text-base justify-center ">
                        <li className="mr-5 list-none hover:text-white cursor-pointer"><Link activeClass="active" to="section1" spy={true} smooth={true} duration={500} >Sobre mi</Link></li>
                        <li className="mr-5 list-none hover:text-white cursor-pointer"><Link activeClass="active" to="section2" spy={true} smooth={true} duration={500} >Proyectos</Link></li>
                        <li className="mr-5 list-none hover:text-white cursor-pointer"><Link activeClass="active" to="section3" spy={true} smooth={true} duration={500} >Skills</Link></li>
                        <li className="mr-5 list-none hover:text-white cursor-pointer"><Link activeClass="active" to="section4" spy={true} smooth={true} duration={500} >Contacto</Link></li>
                    </nav>
                </div>
            </header>
        </>
    )
}

export default Navbar