import React from 'react'
import repository from '../images/github.svg'
import linkedin from '../images/linkedin.svg'
import avatar from '../images/avatar.jpg'

const Footer = () => {
  return (
    <>
    <footer className="bg-cinder-900 body-font">
  <div className="container px-5 py-8 mx-auto flex items-center sm:flex-row flex-col">
    <img src={avatar} className='w-10 rounded-full'/>
    <p className="text-cinder-200 text-sm sm:ml-4 sm:pl-4 sm:border-l-2 sm:text-cinder-200 sm:py-2 sm:mt-0 mt-4">2023 | Ailén Glassmam</p>
    <span className="inline-flex sm:ml-auto sm:mt-0 mt-4 justify-center sm:justify-start">
      <a className="ml-3 md:hover:animate-pulse" href="https://github.com/ailenglassmam" target="_blank">
      <img src={repository} className='w-6'/>
      </a>
      <a className="ml-3 md:hover:animate-pulse" href="https://www.linkedin.com/in/ailenglassmam/" target="_blank">
      <img src={linkedin} className='w-6'/>
      </a>
    </span>
  </div>
</footer>
</>
  )
}

export default Footer