import React, { useState } from 'react'
import AboutMe from './AboutMe';
import avatar from '../images/avatar-2.jpg'
import { Link } from 'react-scroll'

const About = () => {
  //define an state to show
  const [showPersonalInformation, setPersonalInformation] = useState(false);

  return (
    <>
      <div id='about' name='section1'></div>
      <section className="text-cinder-200 bg-cinder-900 body-font sm:mt-8">
        <div className="container h-[90vh] mx-auto flex px-5 py-24 md:flex-row flex-col items-center sm:mt-8 justify-center">
          <div className="lg:max-w-xl lg:w-full md:w-1/2 w-5/6 md:mb-0 mb-10 ">
            <img className="object-cover object-center rounded-full" alt="hero" src={avatar} />
          </div>
          <div className="lg:flex-grow md:w-1/2 lg:pl-24 md:pl-16 flex flex-col md:items-start md:text-left items-center text-center">
            <h1 className="title-font sm:text-6xl text-3xl mb-4 font-medium text-white">Hola, soy Ailén</h1>
            <h2 className="text-scooter-400 text-lg title-font font-medium mb-2">Full Stack Developer</h2>
            <p className="mb-8 leading-relaxed text-lg italic">"No tengas miedo de empezar de nuevo, te puede gustar más tu <span className='font-semibold text-yellow-400'>nueva historia.</span>"</p>
            <div className="flex justify-center">
              <Link className='inline-flex text-white bg-gradient-to-r from-sapphire-600 to-scooter-400 hover:from-scooter-400 hover:to-yellow-400 border-0 py-2 px-6 focus:outline-none rounded uppercase font-bold md:hover:animate-pulse md:hover:cursor-pointer' activeClass="active" to="section4" spy={true} smooth={true} duration={500} >Contactar</Link>
              {/* add an event to show information */}
              <button className="ml-4 inline-flex text-gray-400 bg-gray-800 border-0 py-2 px-6 focus:outline-none hover:bg-gray-700 hover:text-white rounded uppercase font-bold" onClick={() => setPersonalInformation(!showPersonalInformation)}>Sobre mi</button>
            </div>
          </div>
        </div>
        {/* con una consulta ternaria paso el estado: si es true le doy propiedades css, sino es null */}
        <div className={showPersonalInformation ? "container h-auto mx-auto flex px-5 md:flex-row flex-col items-center sm:mt-8 justify-center" : null}>
          {/* the current state && the content is a component + the animation */}
          {showPersonalInformation && <AboutMe></AboutMe>}
        </div>
      </section>

    </>
  )
}

export default About